* {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
}
.sitemap {
  position: fixed;
  z-index: 999;
  padding: 10px;
  width: auto;
  background-color: rgba(255,255,255,0.75);
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  color: #000;
}
body {
  background-color: #fff;
  font-size: 0.83vw;
  max-width: 100vw;
  margin: 0 auto;
  line-height: normal;
  color: #000;
  overflow-x: hidden;
  font-family: 'Roboto Condensed', Arial, Helvetica, sans-serif;
}
h1 {
  text-shadow: 0 0 1.04vw #fff;
  text-align: center;
  color: #005141;
  font-size: 3.33vw;
  font-weight: 700;
  text-transform: uppercase;
  margin: 0;
}
h1.page_title {
  margin: 4.17vw auto 1.46vw;
}
h2 {
  font-size: 1.88vw;
}
h3 {
  font-size: 1.67vw;
}
h4 {
  font-size: 1.46vw;
}
h5 {
  font-size: 1.24vw;
}
h6 {
  font-size: 1.04vw;
}
p {
  font-size: 0.83vw;
}
p.table_title {
  color: #005141;
  font-size: 1.25vw;
  font-weight: 400;
  font-family: 'Roboto Condensed', Arial, Helvetica, sans-serif;
}
b {
  font-weight: bold;
}
table {
  width: 100%;
  margin-bottom: 1.46vw;
}
table tr {
  padding: 0.94vw 0.42vw;
  border-bottom: 1px solid rgba(112,112,112,0.2);
}
table tr:hover {
  background-color: rgba(112,112,112,0.08);
}
table tr td {
  color: #707070;
  font-size: 0.94vw;
  font-family: 'Roboto', Arial, Helvetica, sans-serif;
}
table.custom {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 1.46vw;
}
table.custom tr {
  display: flex;
  align-items: center;
  padding: 0.94vw 0.42vw;
  border-bottom: 1px solid rgba(112,112,112,0.2);
}
table.custom tr:hover {
  background-color: rgba(112,112,112,0.08);
}
table.custom tr td {
  color: #707070;
  font-size: 0.94vw;
  font-family: 'Roboto', Arial, Helvetica, sans-serif;
}
table.custom tr > :first-child {
  flex: 1;
}
table.custom tr > :last-child {
  font-size: 1.04vw;
  white-space: pre;
  flex-shrink: 0;
}
button,
input,
optgroup,
select,
textarea {
  line-height: 1;
}
a {
  text-decoration: none;
  color: inherit;
}
a:hover {
  opacity: 0.8;
}
a.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  text-transform: uppercase;
}
a.btn--tr-green {
  box-shadow: 0 3px 6px rgba(0,0,0,0.16);
  border-radius: 0.52vw;
  border: 1px solid #005141;
  color: #005141;
  font-size: 0.63vw;
  max-width: 8.65vw;
  font-weight: 400;
  line-height: 1;
}
a.btn--tr-blue {
  box-shadow: 0 3px 6px rgba(0,0,0,0.16);
  border-radius: 0.52vw;
  border: 1px solid #4370ff;
  color: #4370ff;
  font-size: 0.63vw;
  max-width: 8.65vw;
  font-weight: 400;
  line-height: 1;
}
a.btn:hover {
  opacity: 0.8;
}
a.btn:active {
  box-shadow: inset 0 3px 6px rgba(0,0,0,0.16);
}
a.btn:active:hover {
  opacity: 1;
}
a.btn--blue {
  background-color: #4370ff;
}
a.btn--red {
  background-color: #ff4349;
}
a.btn--white {
  background-color: #fff;
}
a.btn--calc {
  justify-content: flex-end;
  height: 2.5vw;
  padding: 0 1.88vw;
  position: relative;
  border-bottom: 0.21vw solid #2f58db;
  border-top: 0.21vw solid #4370ff;
  text-transform: uppercase;
  font-size: 1.04vw;
  line-height: 1;
  margin-top: 1.04vw;
  border-radius: 2.6vw 0 0 2.6vw;
}
a.btn--calc:hover {
  opacity: 1;
  border-top: 0.21vw solid #688cff;
  background-color: rgba(67,112,255,0.8);
}
a.btn--calc:hover:before {
  box-shadow: 0 0 6px 3px #ff4349;
}
a.btn--calc:before {
  content: '\ebdf';
  font-family: IcoFont;
  width: 3.02vw;
  height: 3.02vw;
  border-radius: 50%;
  box-shadow: 0 3px 6px #ff4349;
  background-color: #ff4349;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  transition: 0.25s ease-in-out;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  color: #fff;
  font-size: 1.46vw;
}
a.btn--large {
  height: 3.13vw;
  width: 100%;
  max-width: 12.5vw;
  font-size: 1.04vw;
  padding: 0 1.25vw;
}
a.btn--medium {
  height: 2.92vw;
  width: 100%;
  max-width: 15.83vw;
  font-size: 1.04vw;
  padding: 0 1.04vw;
}
a.btn--small {
  height: 1.77vw;
  width: auto;
  max-width: auto;
  font-size: 0.63vw;
  padding: 0 0.83vw;
}
a.btn--request {
  box-shadow: 0 3px 6px rgba(0,0,0,0.16);
  background-color: #005141;
  color: #fff;
  max-width: 16vw;
  margin: 5vw auto 5.5vw;
}
@media screen and (max-width: 1024px) {
  a.btn--large {
    position: relative;
  }
  a.btn--medium {
    height: 9.07vw;
    white-space: pre;
    width: 100%;
    max-width: 44.27vw;
    font-size: 3vw;
    font-weight: 400;
    text-transform: uppercase;
    border-radius: 2.67vw;
  }
  a.btn--small {
    white-space: pre;
    width: 100%;
    display: flex;
    font-size: 3.2vw;
    font-weight: 400;
    text-transform: uppercase;
    height: 9.07vw;
    padding: 0 7.47vw;
    max-width: 32vw;
    border-radius: 2.67vw;
  }
  a.btn--blue {
    color: #fff;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16);
  }
  a.btn--blue:hover {
    opacity: 1;
    background-color: #4370ff;
    box-shadow: 0 3px 6px rgba(0,0,0,0.32);
  }
  a.btn--tr--green {
    height: 9.07vw;
    white-space: pre;
    width: 100%;
    max-width: 44.27vw;
    font-size: 3vw;
    font-weight: 400;
    border-radius: 2.67vw;
    color: #005141;
    background-color: #fff;
    display: flex;
  }
  a.btn--tr--green:hover {
    opacity: 1;
    color: #fff;
    background-color: #005141;
    box-shadow: 0 3px 6px rgba(0,0,0,0.32);
  }
  a.btn--calc {
    justify-content: flex-end;
    height: 9.07vw;
    width: 100%;
    max-width: 53.87vw;
    padding: 0 2.67vw;
    position: relative;
    border-bottom: 0.21vw solid #2f58db;
    border-top: 0.21vw solid #4370ff;
    text-transform: uppercase;
    font-size: 3vw;
    line-height: 1;
    margin: 8vw auto;
    border-radius: calc(9.07vw / 2) 2.67vw 2.67vw calc(9.07vw / 2);
  }
  a.btn--calc:before {
    content: '\ebdf';
    font-family: IcoFont;
    width: 16vw;
    height: 16vw;
    border-radius: 50%;
    box-shadow: 0 3px 6px #ff4349;
    background-color: #ff4349;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    transition: 0.25s ease-in-out;
    top: 50%;
    left: -2.67vw;
    transform: translateY(-50%);
    color: #fff;
    font-size: 7.47vw;
  }
  a.btn--calc:hover {
    opacity: 1;
    border-top: 0.21vw solid #688cff;
    background-color: rgba(67,112,255,0.8);
  }
  a.btn--calc:hover:before {
    box-shadow: 0 0 6px 3px #ff4349;
  }
}
