ol.numeric {
  margin-bottom: 2.08vw;
  list-style-type: none; /* Убираем исходные маркеры */
  counter-reset: item; /* Обнуляем счетчик списка */
}
ol.numeric > :nth-child(1):before,
ol.numeric > :nth-child(2):before,
ol.numeric > :nth-child(3):before,
ol.numeric > :nth-child(4):before,
ol.numeric > :nth-child(5):before,
ol.numeric > :nth-child(6):before,
ol.numeric > :nth-child(7):before,
ol.numeric > :nth-child(8):before,
ol.numeric > :nth-child(9):before {
  content: '0' counter(item); /* Добавляем к числам скобку */
}
ol.numeric > :last-child {
  margin-bottom: 0;
}
ol.numeric li {
  display: flex;
  align-items: flex-start;
  color: #707070;
  font-size: 0.94vw;
  font-weight: 400;
  margin-bottom: 1.04vw;
}
ol.numeric li:before {
  content: counter(item); /* Добавляем к числам скобку */
  counter-increment: item; /* Задаём имя счетчика */
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  color: #005141;
  font-size: 1.04vw;
  font-weight: 400;
  width: 0.94vw;
  height: 0.94vw;
  margin-right: 0.42vw;
}
ul.marker {
  margin-bottom: 2.08vw;
  list-style-type: none; /* Убираем исходные маркеры */
}
ul.marker.marker--v2 li:before {
  border-radius: 0;
  width: 0.52vw;
  height: 0.52vw;
  margin-top: 0.37vw;
}
ul.marker > :last-child {
  margin-bottom: 0;
}
ul.marker li {
  display: flex;
  align-items: flex-start;
  font-family: 'Roboto', Arial, Helvetica, sans-serif;
  color: #707070;
  font-size: 0.94vw;
  font-weight: 400;
  margin-bottom: 1.04vw;
}
ul.marker li:before {
  width: 0.68vw;
  height: 0.68vw;
  content: '';
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  background-color: #005141;
  border-radius: 50%;
  margin-top: 0.21vw;
  margin-right: 0.42vw;
}
@media screen and (max-width: 1024px) {
  ol.numeric {
    margin-bottom: 8vw;
  }
  ol.numeric li {
    font-size: 3.73vw;
  }
  ol.numeric li:before {
    margin-right: 2.13vw;
    margin-top: 1.07vw;
    width: 3.2vw;
    height: 3.2vw;
  }
  ul.marker {
    margin-bottom: 8vw;
  }
  ul.marker.marker--v2 li:before {
    width: 2.67vw;
    margin-top: 1.33vw;
    height: 2.67vw;
  }
  ul.marker li {
