    margin-top: 1.33vw;
    width: 3.2vw;
    height: 3.2vw;
  }
}
section {
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
}
section .title {
  font-size: 2.29vw;
  font-weight: bold;
  text-transform: uppercase;
}
::placeholder {
  color: #005141;
}
input,
textarea {
  text-overflow: ellipsis;
  border: 0;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16);
  background-color: #fff;
  color: #005141;
  font-size: 0.99vw;
  font-weight: 400;
  text-transform: uppercase;
}
input.submit,
textarea.submit,
input[type="submit"],
textarea[type="submit"] {
  background-color: #005141;
  color: #fff;
  cursor: pointer;
}
input.submit:hover,
textarea.submit:hover,
input[type="submit"]:hover,
textarea[type="submit"]:hover {
  opacity: 0.8;
}
header {
  background-color: #fff;
  position: sticky;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  -webkit-box-shadow: 0px 0px 12px 0px #000;
  -moz-box-shadow: 0px 0px 12px 0px #000;
  box-shadow: 0px 0px 12px 0px #000;
}
header svg,
header img {
  height: auto;
  width: 14.43vw;
}
header .container {
  padding: 1.04vw 2.08vw 0.83vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
header .container > :first-child {
  line-height: 0;
}
header .burger {
  width: 2.375rem;
  height: 2.375rem;
  margin-left: 1.5rem;
  color: #fff;
  flex-shrink: 0;
